import { DOCUMENT, NgClass } from '@angular/common';
import { Component, Inject, NgZone, OnDestroy, OnInit } from '@angular/core';
import { Event, NavigationStart, Router, RouterModule } from '@angular/router';
import { User } from '@biz-nest/auth-client';
import { Company, Expense } from '@biz-nest/commerce-client';
import { EventbusService } from '@front-shared-lib';
import { runSubscriptionsForSharedUse } from '@main-shared-lib';
import { FooterComponent } from '../footer/footer.component';
import { NavbarComponent } from '../navbar/navbar.component';
import { SideMenuComponent } from '../side-menu/side-menu.component';
import { FrontendLogger } from '@biz-nest/core-client';

@Component({
  selector: 'app-dashboard',
  standalone: true,
  imports: [
    SideMenuComponent,
    RouterModule,
    NavbarComponent,
    FooterComponent,
    NgClass
  ],
  templateUrl: './dashboard.component.html',
  styleUrl: './dashboard.component.scss'
})
export class DashboardComponent
implements OnInit, OnDestroy {
  adminShow = true;
  cleanup?: () => void;
  isSidebarChecked = false;
  count = 0;
  companys: Company[] = [];
  countUsers = 0;
  countExpenses = 0;
  users: User[] = [];
  expenses: Expense[] = [];
  companyGrowth?: string;
  userGrowth?: string;
  expenseGrpwth?: string;
  profitGrowth?: string;
  menuClosed = false;
  removeSide = false;

  constructor(
    private router: Router,
    private eventbus: EventbusService,
    private zone: NgZone,
    @Inject(DOCUMENT) private document: Document
  ) {
    this.router.events.subscribe((event: Event) => {
      if (event instanceof NavigationStart) {
        if (
          event.url === '/forgot-pass' ||
          event.url === '/lock-screen' ||
          event.url === '/login-form' ||
          event.url === '/register' ||
          event.url === '/error-first' ||
          event.url === '/error-second'
        ) {
          this.adminShow = false;
        } else {
          this.adminShow = true;
        }
      }
    });

    const getWidth = () => window.innerWidth || this.document.documentElement.clientWidth || document.body.clientWidth;
    const width = getWidth();

    if (width < 768) {
      this.menuClosed = true;
      this.removeSide = true;
      this.eventbus.generalEvent$.next({ type: 'removeSide', data: 'true' });
      this.eventbus.generalEvent$.next({ type: 'menuClosed', data: 'true' });
    }
  }

  ngOnInit(): void {
    this.getUsers();
    this.getExpenses();
    this.getCompanys();
    const handlers = {
      toggleMenu: (data: string) => {
        this.zone.run(() => {
          this.menuClosed = data === 'true';
        });
      },
      removeSide: (data: string) => {
        this.zone.run(() => {
          this.removeSide = data === 'true';
        });
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
  }

  async getCompanys() {
    try {
      const { count, dataArr } = await Company.getAll();

      FrontendLogger.debug('get Companys, ', dataArr);
      this.count = count;
      this.companys = dataArr;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getUsers() {
    try {
      const { count, dataArr } = await User
        .getAll();

      this.countUsers = count;
      this.users = dataArr;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getExpenses() {
    const { count, dataArr } = await Expense
      .getAll();

    this.countExpenses = count;
    this.expenses = dataArr;
  }


  ngOnDestroy(): void {
    this.cleanup?.();
  }
}
