/* eslint-disable @typescript-eslint/naming-convention */
import { AfterViewInit, Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { NavigationEnd, Router, RouterModule } from '@angular/router';
import { openClose, shakeBellIcon } from '@front-shared-lib';

class Point {
  private _acceleration = 0;
  private _speed = 0;
  private _radialEffect = 0;
  private _elasticity = 0.001;
  private _friction = 0.0085;
  private _components: { x: number; y: number };

  constructor(
    public azimuth: number,
    public parent: Blob
  ) {
    this.azimuth = Math.PI - azimuth;
    this._components = {
      x: Math.cos(this.azimuth),
      y: Math.sin(this.azimuth)
    };
    this.acceleration = -0.3 + Math.random() * 0.6;
  }

  solveWith(leftPoint: Point, rightPoint: Point): void {
    // eslint-disable-next-line max-len
    this.acceleration = (-0.3 * this.radialEffect + (leftPoint.radialEffect - this.radialEffect) + (rightPoint.radialEffect - this.radialEffect)) * this.elasticity - this.speed * this.friction;
  }

  set acceleration(value: number) {
    if (typeof value === 'number') {
      this._acceleration = value;
      this.speed += this._acceleration * 2;
    }
  }

  get acceleration(): number {
    return this._acceleration;
  }

  set speed(value: number) {
    if (typeof value === 'number') {
      this._speed = value;
      this.radialEffect += this._speed * 5;
    }
  }

  get speed(): number {
    return this._speed;
  }

  set radialEffect(value: number) {
    if (typeof value === 'number') {
      this._radialEffect = value;
    }
  }

  get radialEffect(): number {
    return this._radialEffect;
  }

  get position() {
    return {
      x: this.parent.center.x + this.components.x * (this.parent.radius + this.radialEffect),
      y: this.parent.center.y + this.components.y * (this.parent.radius + this.radialEffect)
    };
  }

  get components() {
    return this._components;
  }

  get elasticity(): number {
    return this._elasticity;
  }

  get friction(): number {
    return this._friction;
  }
}

class Blob {
  points: Point[] = [];
  private _canvas: HTMLCanvasElement | null = null;
  private ctx: CanvasRenderingContext2D | null = null;
  private _color: string | CanvasGradient = '#4caf50';// gradient for canvas
  private _points = 32;
  private _radius = 440; // 1% of container size
  private _position = { x: 0.5, y: 0.5 };
  private _running = true;
  mousePos: { x: number; y: number } | null = null;

  init(): void {
    for (let i = 0; i < this.numPoints; i++) {
      const point = new Point(this.divisional * (i + 1), this);

      this.points.push(point);
    }
  }

  render(): void {
    if (!this.canvas || !this.ctx) {
      return;
    }

    const ctx = this.ctx;
    const pointsArray = this.points;
    const points = this.numPoints;
    const center = this.center;

    ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    pointsArray[0].solveWith(pointsArray[points - 1], pointsArray[1]);

    const p0 = pointsArray[points - 1].position;
    let p1 = pointsArray[0].position;
    const _p2 = p1;

    ctx.beginPath();
    ctx.moveTo(center.x, center.y);
    ctx.moveTo((p0.x + p1.x) / 2, (p0.y + p1.y) / 2);

    for (let i = 1; i < points; i++) {
      pointsArray[i].solveWith(pointsArray[i - 1], pointsArray[i + 1] || pointsArray[0]);

      const p2 = pointsArray[i].position;
      const xc = (p1.x + p2.x) / 2;
      const yc = (p1.y + p2.y) / 2;

      ctx.quadraticCurveTo(p1.x, p1.y, xc, yc);

      p1 = p2;
    }

    const xc = (p1.x + _p2.x) / 2;
    const yc = (p1.y + _p2.y) / 2;

    ctx.quadraticCurveTo(p1.x, p1.y, xc, yc);

    const gradient = this.ctx.createLinearGradient(0, 0, this.canvas.width, this.canvas.height);

    gradient.addColorStop(0, '#4caf50');
    gradient.addColorStop(1, '#2196f3');

    this.color = gradient;

    ctx.fillStyle = this.color;
    ctx.fill();
    ctx.strokeStyle = '#000000';

    if (this.running) {
      requestAnimationFrame(this.render.bind(this));
    }
  }

  set color(value: string | CanvasGradient) {
    this._color = value;
  }

  get color(): string | CanvasGradient {
    return this._color;
  }

  set canvas(value: HTMLCanvasElement | null) {
    if (value instanceof HTMLCanvasElement) {
      this._canvas = value;
      this.ctx = this._canvas.getContext('2d');
    }
  }

  get canvas(): HTMLCanvasElement | null {
    return this._canvas;
  }

  get numPoints(): number {
    return this._points;
  }

  get radius(): number {
    return this._radius;
  }

  set position(value: { x: number; y: number }) {
    if (value && typeof value.x === 'number' && typeof value.y === 'number') {
      this._position = value;
    }
  }

  get position(): { x: number; y: number } {
    return this._position;
  }

  get divisional(): number {
    return Math.PI * 2 / this.numPoints;
  }

  get center(): { x: number; y: number } {
    if (!this.canvas) {
      return { x: 0, y: 0 };
    }

    return {
      x: this.canvas.width * this.position.x,
      y: this.canvas.height * this.position.y
    };
  }

  set running(value: boolean) {
    this._running = value;
  }

  get running(): boolean {
    return this._running;
  }
}

@Component({
  selector: 'lib-ai-blob',
  standalone: true,
  imports: [RouterModule],
  animations: [openClose, shakeBellIcon],
  templateUrl: './ai-blob.component.html',
  styleUrls: ['./ai-blob.component.scss']
})
export class AiBlobComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild('blobCanvas') canvasRef!: ElementRef<HTMLCanvasElement>;
  private blob: Blob;
  private oldMousePoint = { x: 0, y: 0 };
  private hover = false;

  constructor(private router: Router) {
    this.blob = new Blob();
  }

  ngOnInit(): void {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this.blob.running = true;
        // move blob as if mouse is moving
        this.handleMouseMove({
          clientX: window.innerWidth / 2,
          clientY: window.innerHeight / 2,
          preventDefault: () => {},
          stopPropagation: () => {}
        } as PointerEvent);
      }
    });
  }

  ngAfterViewInit(): void {
    this.initializeBlob();
  }

  ngOnDestroy(): void {
    this.blob.running = false;
  }

  private initializeBlob(): void {
    const canvas = this.canvasRef.nativeElement;

    canvas.setAttribute('touch-action', 'none');
    const resize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    window.addEventListener('resize', resize);
    resize();

    this.blob.canvas = canvas;
    this.blob.init();
    this.blob.render();

    window.addEventListener('pointermove', this.handleMouseMove.bind(this));
  }

  private handleMouseMove(e: PointerEvent): void {
    const pos = this.blob.center;
    const diff = { x: e.clientX - pos.x, y: e.clientY - pos.y };
    const dist = Math.sqrt((diff.x * diff.x) + (diff.y * diff.y));
    let angle: number | null = null;

    this.blob.mousePos = { x: pos.x - e.clientX, y: pos.y - e.clientY };

    if (dist < this.blob.radius && !this.hover) {
      angle = Math.atan2(diff.y, diff.x);
      this.hover = true;
    } else if (dist > this.blob.radius && this.hover) {
      angle = Math.atan2(diff.y, diff.x);
      this.hover = false;
      this.blob.color = '#000000';
    }

    if (typeof angle === 'number') {
      let nearestPoint: Point | null = null;
      let distanceFromPoint = 400;

      this.blob.points.forEach((point) => {
        if (Math.abs(angle! - point.azimuth) < distanceFromPoint) {
          nearestPoint = point;
          distanceFromPoint = Math.abs(angle! - point.azimuth);
        }
      });

      if (nearestPoint) {
        const strength = {
          x: this.oldMousePoint.x - e.clientX,
          y: this.oldMousePoint.y - e.clientY
        };
        let strengthValue = Math.sqrt((strength.x * strength.x) + (strength.y * strength.y)) * 10;

        if (strengthValue > 100) {
          strengthValue = 100;
          (nearestPoint as Point).acceleration = strengthValue / 100 * (this.hover ? -1 : 1);
        }
      }
    }

    this.oldMousePoint.x = e.clientX;
    this.oldMousePoint.y = e.clientY;
  }
}
