import { Router } from '@angular/router';
import { FrontendLogger } from '@biz-nest/core-client';
import { ToastrService } from 'ngx-toastr';

export class MainHeaderBase {
  searchQuery = '';

  constructor(
    protected router: Router,
    protected toastr: ToastrService
  ) {}

  makeSearch(routeMap: { [key: string]: string }) {
    const query = this.searchQuery?.trim().toLowerCase(); // Added safe navigation

    FrontendLogger.debug('searchQuery is ', query);
    FrontendLogger.debug('routeMap is ', routeMap);

    if (!query) {
      FrontendLogger.debug('Search query is empty, doing nothing.');

      return; // Do nothing if the search is empty or null
    }

    FrontendLogger.debug(`Searching for route starting with: "${query}"`);

    let foundRoute: string | null = null;
    let matchedKey: string | null = null;

    // Iterate over the keys of the routeMap
    for (const key in routeMap) {
      // Ensure it's a property of the object itself, not from the prototype chain
      if (Object.prototype.hasOwnProperty.call(routeMap, key)) {
        // Check if the key starts with the user's query
        if (key.startsWith(query)) {
          foundRoute = routeMap[key];
          matchedKey = key;
          FrontendLogger.debug(`Found match: key="${matchedKey}", route="${foundRoute}"`);
          break; // Found the first match, stop searching
        }
      }
    }

    // The Navigate if a route was found
    if (foundRoute) {
      FrontendLogger.debug(`Navigating to route: ${foundRoute}`);
      this.router.navigate([foundRoute]);
      this.searchQuery = ''; // Clear search bar after successful navigation
    } else {
      // No route found, inform the user
      FrontendLogger.warn(`No route found starting with search query: "${query}"`);
      // Use the original searchQuery for the toast message for clarity
      this.toastr.info(`Could not find a page matching "${this.searchQuery}"`, 'Navigation');
    }
  }
}
