import { DOCUMENT, NgClass } from '@angular/common';
import { Component, Inject, NgZone, OnDestroy, OnInit } from '@angular/core';
import { Event, NavigationEnd, Router, RouterModule } from '@angular/router';
import { NotificationMain } from '@biz-nest/notification-client';
import { AuthService, EventbusService } from '@front-shared-lib';
import { runSubscriptionsForSharedUse } from '@main-shared-lib';
import { AdminLogoutSpinnerComponent } from '../../misc/admin-logout-spinner/admin-logout-spinner.component';

@Component({
  selector: 'app-side-menu',
  standalone: true,
  imports: [
    RouterModule,
    NgClass,
    AdminLogoutSpinnerComponent
  ],
  templateUrl: './side-menu.component.html',
  styleUrl: './side-menu.component.scss'
})
export class SideMenuComponent
implements OnInit, OnDestroy {
  showModal = false;
  showDropdown = true;
  bellCollapsed = true;
  userCollapsed = true;
  langCollapsed = true;
  splitVal;
  base = '';
  page = '';
  notifications: NotificationMain[] = [];
  loading = false;
  collapseShow = 'hidden';
  menuClosed = false;
  removeSide = false;
  cleanup?: () => void;

  constructor(
    private auth: AuthService,
    private router: Router,
    private eventbus: EventbusService,
    private zone: NgZone,
    @Inject(DOCUMENT) private document: Document
  ) {
    this.router.events?.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.splitVal = event.url.split('/');
        this.base = this.splitVal[1];
        this.page = this.splitVal[2];
      }
    });

    const getWidth = () => window.innerWidth ||
    this.document.documentElement.clientWidth || this.document.body.clientWidth;
    const width = getWidth();

    if (width < 768) {
      this.menuClosed = true;
      this.removeSide = true;
      this.eventbus.generalEvent$.next({ type: 'removeSide', data: 'true' });
      this.eventbus.generalEvent$.next({ type: 'menuClosed', data: 'true' });
    }
  }

  ngOnInit(): void {
    // this.initActiveMenu();
    const handlers = {
      toggleMenu: (data: 'true' | 'false') => {
        this.zone.run(() => {
          this.menuClosed = data === 'true';
        });
      },
      removeSide: (data: 'true' | 'false') => {
        this.zone.run(() => {
          this.removeSide = data === 'true';
        });
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
  }

  toggleCollapseShow(classes) {
    this.collapseShow = classes;
  }


  async logOut() {
    this.showModal = true;
    await this.auth.logOut();
    this.showModal = false;
  }


  ngOnDestroy(): void {
    this.cleanup?.();
  }
}
